
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  }
  /*i use css variaables */
  :root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);
  
    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-ms:90%;
  
    --transition: all 400ms ease;
  }



.about__container{
    /* display: grid;
    grid-template-columns: 35% 50%; */
    grid-template-columns: 1fr;
    gap: 0;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,    
        var(--color-primary),   
        transparent
    );
    display: grid;
    place-items: center;

    width: 50%;
    margin: 2rem auto 4rem;

}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    height: 100%;
    transform:rotate(10deg) ;
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.5rem;
}
.about__card{
    background: var(--color-bg-variant);
    border:1px solid transparent  ;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color:var(--color-primary-variant)  ;
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 0.95rem;
}


.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    /* margin: 2rem 0 2.6rem; */
    color: var(--color-light);
    margin: 1rem 0  1.5rem;

}







@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0  1.5rem;
    }
}
@media screen and (max-width:600px){
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__content p{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
}








